@import url('https://fonts.googleapis.com/css?family=Josefin+Sans&display=swap');
@import "staged";

body {
  margin: 0;
  background-color: #393939;
  color: #d0d0d0;
  font-family: 'Josefin Sans', sans-serif;
}

.App {
  padding-bottom: 1em;
  position: relative;

  img {
    cursor: pointer;
    display: block;
  }

  .description {
    padding: 0 0.5rem;
  }
}

.example-1 {
  margin-bottom: 2em;
  img {
    width: 100%;
    height: 20vw;
  }
}

.example-2 {
  margin-bottom: 2em;

  .carousel {
    padding: 0 4em;
  }

  input {
    margin-bottom: 1em;
  }

  img {
    width: 100%;
    height: 50vw;
  }
}

.example-3 {
  margin-bottom: 2em;
  img {
    width: 100%;
    height: 30vw;
  }
}

.example-4 {
  margin-bottom: 2em;
  img {
    width: 100%;
    height: 60vw;
  }
}

.example-5 {
  margin-bottom: 2em;

  .article {
    padding: 1rem;

    .cover {
      height: 10vw;
      @media (max-width: 650px) {
        height: 30vw;
      }
      margin-bottom: 0.5rem;
      img {
        width: 100%;
        max-height: 100%;
        box-shadow: 0 0 6px 3px black;
      }
    }

    .title {
      font-size: 1.2rem;
      font-weight: bold;
      margin-bottom: 0.5rem;
    }
  }

  .staged-outer {
    box-shadow: 0 0 6px 3px black;
    background-color: #ababab;
    color: #222222;
  }
}

.example-6,.example-7 {
  margin-bottom: 1em;

  .huge-num-container {
    margin: 1em;
    background-color: #ababab;
    height: 9em;
    text-align: center;

    .huge-num {
      font-size: 10em;
      color: #222222;
      opacity: 0.5;
    }
  }
}

.example-7 {
  h2 {
    display: inline-block;
  }
  .buttons {
    display: inline-block;

    button {
      background-color: #112765;
      outline: 0;
      margin-left: 0.5rem;
      padding: 0.25rem 0.5rem;
      color: white;
      border: none;
      border-radius: 0.25rem;
      font-size: 1em;
    }
  }
}

.example-8 {
  img {
    width: 100%;
    height: 60vw;
  }

  .container {
    position: relative;

    &__bg {
      position: absolute;
    }

    &__fg img {
      transform: scale(0.5) translateY(-40%) rotate(calc(1turn * (var(--s-transform-x) / var(--s-width))));
      transition: transform var(--s-trans-dur) var(--s-trans-animation);
    }
  }
}

img.clicked {
  animation: opacity-click 250ms linear;
}

@keyframes opacity-click {
  0% { opacity: 1 }
  50% { opacity: 0 }
  100% { opacity: 1 }
}
